<template>
  <div>
    <!--  <h2>产品列表</h2>-->
    <!--  1.搜索添加位置-->
    <div class="header">
      <div class="form">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="产品名称">
            <el-input v-model="formInline.user"  @blur="blueClick" placeholder="产品名称"></el-input>
          </el-form-item>
          <el-form-item label="日期选择">
            <el-date-picker
                v-model="formInline.date"
                type="date"
                size="small"
                placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="group">
        <el-row>
          <el-button size="small" icon="el-icon-plus" type="warning">添加</el-button>
          <el-button size="small" icon="el-icon-delete" type="danger">删除</el-button>
        </el-row>
      </div>
    </div>
    <div class="content">
      <el-table :data="tableData" style="width: 100%;" border header-cell-class-name="active-header"
                cell-class-name="table-center" @select="selectHandle" @select-all="selectHandle">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column prop="id" label="商品编号" width="120">
        </el-table-column>
        <el-table-column prop="title" label="商品名称" width="120" show-overflow-tooltip>
          <template slot-scope="scope">
            <span style="color:blue;cursor: pointer;" @click="handleLook(scope.$index, scope.row)">{{
                scope.row.title
              }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="price" label="商品价格" width="120">
        </el-table-column>
        <el-table-column prop="category" label="商品类目" width="120">
        </el-table-column>
        <el-table-column label="添加时间">
          <template slot-scope="scope">
            <span>{{ dayjs(scope.row.create_time).format('YYYY-MM-DD HH:mm:ss') }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="sellPoint" label="商品卖点" show-overflow-tooltip>
        </el-table-column>
        <el-table-column label="商品描述" show-overflow-tooltip>
          <template slot-scope="scope">
            <span v-if="!!scope.row.descs">{{ removeHTMLTag(scope.row.descs) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)"
                       icon="el-icon-edit">编辑
            </el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"
                       icon="el-icon-delete">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="p10 bgfff">
        <Pagination :total="total" :pageSize="pageSize" @CurrentChange="CurrentChange"></Pagination>
      </div>

    </div>
    <!--  1.产品列表-->
  </div>
</template>

<script>
import Pagination from "@/components/pagination/Pagination";
import dayjs from 'dayjs'
import {removeHTMLTag} from '@/utils/common'

export default {
  name: "index",
  components: {
    Pagination,
  },
  data() {
    return {
      formInline: {
        user: '',
        date: ''
      },
      tableData: [{
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-04',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1517 弄'
      }, {
        date: '2016-05-01',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1519 弄'
      }, {
        date: '2016-05-03',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1516 弄'
      }],
      total: 1,
      pageSize: 1,
      conert: 1,
    }
  },
  created() {
    this.projectList(1)
  },
  methods: {
    dayjs,
    removeHTMLTag,
    CurrentChange(val) {
      console.log(val, '----------')
      this.conert =val
      this.projectList(val)
    },

    onSubmit() {
      console.log('submit!', 'formInline', this.formInline.user);
      this.search(this.formInline.user)
    },
    blueClick() {
      if (!this.formInline.user) {
        this.projectList(1)
      }
    },

    //获取产品列表数据-------------------------------------
    async projectList(page) {
      let res = await this.$api.projectList({page})
      console.log('产品列表数据----', res.data);
      this.tableData = res.data.data;
      this.total = res.data.total
      this.pageSize = res.data.pageSize;
    },
    //删除数据-------------------------------------
    async deleteItemById(id) {
      let res = await this.$api.deleteItemById({id})
      if (res.data.status ==200){
        this.$message({
          type: 'success',
          message: '删除成功'
        });
        this.projectList(this.conert)
      }
      console.log('删除数据----', res.data);
    },
    //获取产品搜索接口-------------------------------------
    async search(search) {
      if (!search) {
        return
      }
      let res = await this.$api.search({search})
      console.log('产品列表数据----', res.data);
      this.tableData = res.data.result;
      this.total =  this.tableData.length
      this.pageSize =this.tableData.length
    },
    //查看商品详情操作---------------------------
    handleLook(index, row) {
      // this.changeRowData(row)
      // this.changeTitle('详情')
      // //跳转编辑界面------------------
      // this.$router.push('/product/add-product')
    },
    //点击勾选选择框------选中id------------------------
    selectHandle(selection) {
      console.log('selection---', selection);
      let arr = []
      selection.forEach(ele => {
        arr.push(ele.id)
      });
      this.ids = arr;
    },
    //编辑操作------------------------------------------
    handleEdit(index, row) {
      console.log(index, row);
      //存储当前行的数据--vuex---跳转到另外一个界面--获取vuex行数据
      // this.changeRowData(row)
      // this.changeTitle('编辑')
      //跳转编辑界面------------------
      // this.$router.push('/product/add-product')
    },
    //删除操作--------------------------------------------
    handleDelete(index, row) {
      console.log('删除操作---', index, row);
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        //删除这个数据---请求后台接口----同步数据库-----------------
        if (this.tableData.length ==1){
          this.conert --
        }
        this.deleteItemById(row.id)
        // this.$message({
        //   type: 'success',
        //   message: '删除成功'
        // });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });

    },
  },
}
</script>

<style lang="less" scoped>
.header {
  background: #fff;
  margin-bottom: 20px;
}

.content {
  background: #fff;
  padding: 8px;

  /deep/ .active-header {
    color: #333;
    text-align: center;
  }

  /deep/ .table-center {
    text-align: center;
  }
}
</style>
